@import 'src/style/variable.scss';

.totalprice {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-direction: column;
  padding-top: 30px;
  &__block {
    color: var(--searchBlock);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    .sale {
      color: var(--oldRed);
    }
  }

  &__text {
    justify-self: start;

    &.notprice {
      p {
        line-height: 130% !important;
      }
    }
  }

  @media (max-width: $mw4 + px) {
    & {
      max-width: 250px;
    }
  }
  @media (max-width: $mw5 + px) {
    & {
      width: 312px;
    }
    &__block {
      .sale {
        color: var(--oldRed);
      }
    }
  }
  @media (max-width: 312px) {
    & {
      width: 100%;
    }
  }
}
