@import 'src/style/variable.scss';

.basket {
  width: 100%;
  height: 100%;
  padding-top: calc(140px + (180 - 140) * ((100vw - 320px) / (1100 - 320)));
  &__row {
    min-height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  &__aside {
    width: 300px;
    grid-column: 3/4;
  }
  &__price {
    max-width: 300px;
    width: 100%;
    display: flex;
    flex-direction: column;

    box-shadow: 0 0 10px var(--borderforschedul);
    border-radius: 5px;
    padding: 30px 20px;
    transition: margin 0.5s ease 0s;
    &.show {
      top: 0px;
      position: fixed;
    }
    &.bottom {
      bottom: 0;
      top: auto;
      position: absolute;
    }
    &.showtwo {
      margin-top: 83px;
    }
  }
}

.pageonebas {
  width: 75%;
  height: 100%;
  display: grid;
  grid-template: 1fr / 1fr 40px auto;
  position: relative;
  &__header {
    display: flex;
    width: 75%;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 48px;
  }

  &__logo {
    display: flex;
    align-items: center;
    color: var(--searchBlock);
  }

  &__img {
    width: 25px;
    height: 25px;
    margin-right: 10px;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--searchBlock);
    }
  }
  &__clear {
    color: var(--borderInput);
    cursor: pointer;
    &:hover {
      color: var(--searchBlock);
    }
  }
  &__btn {
    margin-top: 30px;
    width: 100%;
  }
}
@media (max-width: $mw1 + px) {
  .pageonebas {
    width: 100%;
    &__header {
      width: 100%;
    }
  }
}
@media (max-width: $mw2 + px) {
  .pageonebas {
    width: 100%;
  }
}

@media (max-width: $mw4 + px) {
  .pageonebas {
    grid-template: 1fr 30px auto / 1fr;
    &__header {
      margin-bottom: 30px;
    }
  }
  .basket {
    &__aside {
      width: 100%;
      grid-row: 3/4;
      grid-column: 1/2;
    }
    &__price {
      max-width: 800px;
    }
  }
}

@media (max-width: $mw4 + px) {
  .basket {
    &__price {
      flex-direction: column;
      align-items: start;
      height: auto;
    }
  }
}
