@import 'src/style/variable.scss';

.productbasket {
  width: 100%;
  height: 100%;

  &__goods {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  &__btnsecond {
    display: none;
  }

  &__item {
    width: 100%;
    height: 200px;
    display: grid;
    grid-template: 1fr / auto 1fr 1fr auto;
    align-items: center;
    padding: 20px 0;
    border-top: 1px solid var(--borderforschedul);
    &:last-child {
      border-bottom: 1px solid var(--borderforschedul);
    }
  }

  &__first {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
  }
  &__category {
    padding-top: 0px;
    color: var(--borderInput);
  }

  &__blockimg {
    height: 100%;
    width: 170px;
    background: var(--goodsBack);
    border-radius: 5px;
    padding: 10px;

    cursor: pointer;
  }

  &__img {
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__second {
    padding-left: 20px;
  }

  &__text {
    color: var(--searchBlock);
    margin-bottom: 20px;
  }

  &__addblock {
    justify-self: center;
    height: 100%;
    display: grid;
    grid-template: 1fr/ 40px 50px 40px;
    justify-items: center;
    align-items: center;
    user-select: none;
  }

  &__count {
    color: var(--searchBlock);
    font-weight: bold;
  }

  &__btn {
    border-radius: 10px;
    display: flex;
    width: 40px;
    height: 40px;
    background: var(--borderforschedul);
    justify-content: center;
    align-items: center;
    cursor: pointer;
    opacity: 0.7;
    svg {
      fill: var(--searchBlock);
      width: 22px;
      height: 22px;
    }
    &:hover {
      opacity: 1;
    }
    &:active {
      width: 40px;
      height: 40px;
      opacity: 1;
    }
    &:active svg {
      width: 20px;
      height: 20px;
    }
  }

  &__midst {
    justify-self: center;
    display: flex;
    align-items: center;
  }

  &__price {
    height: 100%;
    width: 100%;
    color: var(--searchBlock);
    display: flex;

    .old {
      text-decoration: line-through;

      opacity: 0.8;
    }

    .sale {
      color: var(--oldRed);
      padding-right: 10px;
    }

    .through {
      text-decoration: line-through;
      opacity: 0.8;
    }

    .newcols {
      padding-right: 10px;
    }
  }

  &__end {
    height: 100%;
    width: auto;
    display: grid;
    grid-template: 1fr /1fr 45px;
    justify-items: end;
  }

  &__delete {
    width: 20px;
    height: 20px;
    opacity: 0.7;
    cursor: pointer;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--searchBlock);
    }
  }

  &__delete:hover {
    opacity: 1 !important;
  }
  @media (max-width: 400px) {
    &__item {
      height: 180px;
      grid-template: 1fr auto auto / auto 2fr auto;
      align-items: center;
      padding: 20px 0;
      justify-items: start;
      &:last-child {
      }
    }
    &__second {
      padding-left: 0px;
    }
    &__price {
      width: 100%;

      .old {
        padding-right: 0px;
      }
    }
    &__blockimg {
      width: 120px;
      padding: 5px;
    }
    &__first {
      grid-column: 1/2;
      grid-row: 1/4;
      padding-right: 20px;
    }

    &__addblock {
      margin: 0;
      grid-column: 2/3;
      grid-row: 3/4;
      height: 35px;
      width: 100%;
      grid-template: 1fr/35px 30px 35px;
    }

    &__btn {
      width: 35px;
      height: 35px;
      padding: 10px;

      svg {
        width: 100%;
        height: 100%;
        opacity: 0.7 !important;
      }
    }

    &__text {
      align-self: start;
      grid-column: 2/4;
      grid-row: 1/2;
      padding: 0;
    }

    &__midst {
      justify-self: start;
      grid-column: 2/4;
      grid-row: 2/3;
      padding-bottom: 10px;
    }

    &__end {
      grid-column: 3/4;
      grid-row: 1/4;
      height: 100%;
      width: auto;
      display: grid;
      grid-template: 1fr /1fr 40px;
      align-items: start;
      justify-self: end;
    }

    &__btnsecond {
      display: block;
    }
    &__btnfirst {
      display: none;
    }
  }

  @media (max-width: 360px) {
    &__item {
      height: 190px;
      grid-template: 1fr auto auto / auto 2fr auto;
      padding: 25px 0;
    }
    &__blockimg {
      width: 100px;
      padding: 5px;
    }
    &__first {
      padding-right: 15px;
    }
    &__end {
      grid-template-columns: 1fr;
    }

    &__delete {
      display: none;
    }
  }
}
